import { SET_INSTANCE_CREATION_MODAL, SET_IS_MENU_OPEN } from "../actions/types"

const stateSchema = {
    isActive: false,
    modalShowing: false,
    data: {}

}

const initialState = {
    playground: { ...stateSchema },
    item: { ...stateSchema },
    itemCategory: { ...stateSchema },
    vendor: { ...stateSchema },
    customer: { ...stateSchema },
    account: { ...stateSchema }, //for account types
    acc: { ...stateSchema }, //for accounts
}


//----------------------------------------------------------
//------REDUCER FOR CONTROLLING THE INSTANCE CREATION MODALS
//----------------------------------------------------------

export default (state = initialState, action) => {
    switch (action.type) {
        case SET_IS_MENU_OPEN:
            return {
                ...state,
                [action.payload[0]]: {
                    ...state[action.payload[0]],
                    isActive: action.payload[1]
                }
            }

        case SET_INSTANCE_CREATION_MODAL:
            return {
                ...state,
                [action.payload[0]]: {
                    ...state[action.payload[0]],
                    modalShowing: action.payload[1]
                }
            }
        default:
            return state
    }
}