import store from "../redux/store";
import jwtDecode from "jwt-decode";
import { SET_USER } from "../globals/actions";
import JwtDecode from "jwt-decode";
import { automaticLogout, automaticLogout2 } from "../redux/actions/authActions";
import DEMO from "../globals/constant";
import Jhilikka from "../App/components/Reusables/Jhilikka";

const serverUrl = `${DEMO.SERVER_URL}/`


const downloadFile = (blob, filename, format) => {
  const url = window.URL.createObjectURL(new Blob([blob]));
  const link = document.createElement('a');
  link.href = url;
  link.setAttribute('download', filename + format);
  document.body.appendChild(link);
  link.click();
  link.parentNode.removeChild(link);
}


const getResponse = async (response, todo) => {
  const contentType = response.headers.get("content-type");
  if (contentType === "application/json") {
    const json = await response.json();
    return { response, json };
  }


  if (!todo) {
    if (contentType === 'application/pdf' || contentType === 'application/ms-excel') {
      response.blob().then(
        (blob) => {
          const url = window.URL.createObjectURL(new Blob([blob]));
          const link = document.createElement('a');
          link.href = url;
          let format = '.pdf';
          if (contentType === 'application/ms-excel') format = '.xlsx';
          link.setAttribute('download', `export` + `${(new Date()).getTime()}` + format);
          document.body.appendChild(link);
          link.click();
          link.parentNode.removeChild(link);
        }
      )
      const json = { 'status': true }
      return { response, json }
    }
  } else {
    // if the file has to be printed
    if (contentType === 'application/pdf') {
      console.log(response)
      return response
        .blob()
        .then((myBlob) => {
          return { response, json: { status: true, blob: myBlob } }
        })
    }

    if (contentType === 'application/zip') {
      response.blob().then((blob) => {
        downloadFile(blob, 'backup-file-' + (new Date()).toDateString(), '.zip')
      })

      const json = { 'status': true }
      return { response, json }
    }
  };
}


const baseRequest = async (url, method, body, signal, todo) => {

  const errorMessage = (message) => (
    { json: { status: false, error: message || 'Session Expired, Try to re-login!' } }
  )

  try {
    const userState = store.getState().auth.user;

    // always call main call api
    const mainCall = async () => {
      return await fetch(`${serverUrl}${url}`, {
        method: method,
        credentials: "include",
        body: method !== 'GET' ? JSON.stringify(body) : null,
        signal: signal,
        headers: {
          "Content-Type": "application/json",
          'Authorization': `JWT ${userState.accessToken}`,
        }
      });
    }
    try {
      var response = await getResponse(await mainCall(), todo);
      // console.log(response)
    } catch (e) {
      throw e;
    }

    if (!response.response.ok) {
      Jhilikka.fire({ icon: 'error', title: response.response.statusText + '. Please, Try after re-logging in.', background: '#ffe5de' })
      console.log(response.response)
      if (response.response.status === 500) {
        Jhilikka.fire({ icon: 'error', title: 'Internal Server Error, Try Again!', background: '#ffe5de' })
        return;
      }
      store.dispatch(automaticLogout());
    }

    if (!response.json.status) {
      console.log("Error:", response.json.error);
      if (
        response.json.error === "ValidationError: [ErrorDetail(string='Signature has expired.', code='invalid')]" ||
        response.json.error === `Error: ValidationError: [ErrorDetail(string="User doesn't exist.", code='invalid')]`
      ) {
        console.log('Token expired');
        store.dispatch(automaticLogout());
        return;

        //try refreshing
        const res6 = await fetch(`${serverUrl}api/v1/user/refresh`, {
          method: 'POST',
          body: JSON.stringify({ token: userState.accessToken }),
          headers: {
            "Content-Type": "application/json",
          }
        });

        try {
          var responseOfRefresh = await getResponse(res6);
          console.log(response)
          console.log('********************************')
          console.log('********************************')
          console.log('********************************')
          console.log('********************************')
        } catch (e) {
          throw e;
        }

        console.log(responseOfRefresh, 'is the resposne of refresh')
        //if fails logout all the way
        console.log(responseOfRefresh.json);
        if (!responseOfRefresh.response.ok) {
          store.dispatch(automaticLogout());
          return
        }
        if (!responseOfRefresh.json.status) {
          store.dispatch(automaticLogout());
          return
        }

        console.log('refresh is successful')

        // update oldState to new one
        const oldState = JSON.parse(localStorage.getItem('user_state'));
        const newToken = (await getResponse(responseOfRefresh)).json.token
        console.log("New token we received is: ", JwtDecode(newToken))
        const newState = { ...oldState, accessToken: newToken };
        localStorage.setItem('user_state', JSON.stringify(newState))
        await store.dispatch({ type: SET_USER, payload: newState });

        //Recall the api call again!! and if fails should sign in again
        response = await getResponse(await mainCall(), todo);
        if (!response.response.ok) {
          store.dispatch(automaticLogout());
          return;
        }
        if (!response.json.status) {
          store.dispatch(automaticLogout());
          return
        }
      }
    }

    return response
  } catch (e) {
    console.log(e);
    // Jhilikka.fire({ icon: 'error', title: e + '. Or Check your internet connection.', background: '#ffe5de' })
  }
}


const normalRequest = async (url, method, body, signal) => {

  // always call main call api
  const mainCall = async () => {
    return await fetch(`${serverUrl}${url}`, {
      method: method,
      credentials: "include",
      body: JSON.stringify(body),
      signal: signal,
      headers: {
        "Content-Type": "application/json",
      }
    });
  }

  var response = await getResponse(await mainCall());

  return response
}

export { baseRequest, normalRequest };