import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { Router } from 'react-router-dom';

import App from './App/index';
import * as serviceWorker from './serviceWorker';
import store from './redux/store';

import { createBrowserHistory } from 'history'
import * as actionTypes from './globals/actions';
import { SET_SETTINGS } from './redux/actions/types';


if (localStorage.getItem('user_state')) {
    const userState = JSON.parse(localStorage.getItem('user_state'));
    store.dispatch({ type: actionTypes.SET_USER, payload: userState });
}


if (localStorage.getItem('user_settings')) {
    const userSettings = JSON.parse(localStorage.getItem('user_settings'));
    store.dispatch({ type: SET_SETTINGS, payload: userSettings });
}

export const history = createBrowserHistory();

if (process.env.NODE_ENV !== 'development') {
    console.log = () => { }
}


const app = (
    <Provider store={store}>
        <Router history={history} >
            {/* basename="/route-routebase" */}
            <App />
        </Router>
    </Provider>
);

ReactDOM.render(app, document.getElementById('app-root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: http://bit.ly/CRA-PWA
serviceWorker.unregister();
