export const setSettings = (settings) => {

    localStorage.setItem('user_settings', JSON.stringify(settings))
    return {
        type: SET_SETTINGS,
        payload: settings
    }
}


export const resetSettings = () => {

    return {
        type: RESET_SETTINGS,
        payload: {}
    }
}
