import React, { Suspense } from 'react';
import { Switch, withRouter, Route, Redirect } from 'react-router-dom';

import '../../node_modules/font-awesome/scss/font-awesome.scss';
import Loader from './layout/Loader'
import Aux from "../hoc/_Aux";
import ScrollToTop from './layout/ScrollToTop';
import pubRoutes from "../pubRoutes";
import AdminLayout from './layout/AdminLayout'
import PublicRoute from '../hoc/PublicRoute';
import UserManual from '../Pages/Docs/UserManual';





const App = (props) => {
    //auth pubRoutes are here
    const pubMenu = pubRoutes.map((route, index) => {
        return (route.component) ? (
            <PublicRoute
                key={index}
                path={route.path}
                exact={route.exact}
                name={route.name}
                component={propps => (
                    <route.component {...propps} />
                )} />
        ) : (null);
    });

    return (
        <Aux>
            <ScrollToTop>
                <Suspense fallback={<Loader />}>
                    <Switch>
                        {pubMenu}
                        <Route path={'/user-manual'} exact={true} name={'User Manual'} component={UserManual} />
                        <Route exact path="/">
                            <Redirect to="/dashboard" />
                        </Route>
                        <AdminLayout />
                    </Switch>
                </Suspense>
            </ScrollToTop>
        </Aux>
    );
}



export default withRouter(App);
