export function isAccessible(givenPath) {

    if (!store.getState().auth.isAuthenticated) {
        window.location.href = '/signin';
        return false;
    }
    const requiredPowers = structuredPowers[givenPath];
    const powersOfUser = store.getState().auth.user.powers;
    let isAccessible = true;
    if (isEmpty(requiredPowers)) {
        return true;
    }

    for (let requiredPower of requiredPowers) {
        //if power is mentioned and is true or false
        if (powersOfUser[requiredPower] !== null && powersOfUser[requiredPower] !== undefined) {

            //has power to
            if (powersOfUser[requiredPower] === true) {
                isAccessible = isAccessible && true;
            } else { // do not have power to
                isAccessible = isAccessible && false;
            }
        } else { //if power is not mentioned
            isAccessible = isAccessible && true;
        }
    }

    if (!isAccessible) {
        console.log("You have not priveledge to visit this route!")
    }

    return isAccessible
}