import React, { Component } from 'react';
import { Dropdown, Badge } from 'react-bootstrap';
import PropTypes from 'prop-types'


import ChatList from './ChatList';
import Aux from "../../../../../hoc/_Aux";

import Avatar2 from '../../../../../assets/images/user/avatar-2.jpg';
import Avatar1 from '../../../../../assets/images/user/avatar-2.jpg';
import Avatar3 from '../../../../../assets/images/user/avatar-2.jpg';
import Avatar from '../../../../../assets/images/user/avatar.jpg';
import { withRouter, NavLink } from 'react-router-dom';
import { connect } from 'react-redux';
import { logout } from '../../../../../redux/actions/authActions';
import { isAccessible } from '../../../../../helpers/protectRoute';
import { getNotifications, readNotifications } from '../../../../../api/userManagement/notifications';
import Jhilikka from '../../../../components/Reusables/Jhilikka';
import isEmpty from '../../../../../validation/isEmpty';
import useMousetrap from 'react-hook-mousetrap';

const NavRight = (props) => {


    const [notificationsArr, setNotificationsArr] = React.useState([]);
    const [state, setState] = React.useState({
        listOpen: false,
        gearOpen: false,
    });
    const [unreadCount, setUnreadCount] = React.useState(0);

    React.useEffect(() => {


        const requestJson = {
            "action": "get",
            "start": 0,
            "end": 20,
            "read": null,
        };

        const getNotificationsArr = () => {
            getNotifications(requestJson)
                .then((res) => {
                    if (res.json.status) {
                        setNotificationsArr(res.json.notifications);
                        let count = 0;
                        for (let not of res.json.notifications) {
                            if (!not.read) count++;
                        }
                        if (count > 0) {
                            setUnreadCount(count);
                        }

                    } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
                })
                .catch((err) => {
                    console.log(err)
                });
        }

        getNotificationsArr();
        // const poler = setInterval(getNotificationsArr, 5000)



        return () => {
            // clearInterval(poler);
        }
    }, []);

    const _navigate = (path) => {
        props.history.push(path)
    }

    const _hideAndGo = (path) => {
        props.history.push(path)
    }

    const _logout = () => {
        props.logout();
    }

    const authLinks = [
        {
            pathname: '/logs',
            path: '/logs',
            title: 'Logs',
            icon: 'fa fa-file-text-o'
        },
        {
            pathname: '/users',
            path: '/users',
            title: 'Users',
            icon: 'fa fa-users'
        },
        {
            pathname: '/roles',
            path: '/roles',
            title: 'Roles',
            icon: 'feather icon-user-x'
        }
    ]

    const createShortcuts = [
        { pathname: '/item/create', title: 'Item', },
        { pathname: '/vendor/create', title: 'Vendor', },
        { pathname: '/customer/create', title: 'Customer', },
        { pathname: '/purchase-order/create', title: 'Purchase Order (ALT+P)', },
        { pathname: '/invoice/create', title: 'Invoice (ALT+I)', },
        { pathname: '/account/create', title: 'Account ', },
        { pathname: '/ledger-entry/create', title: 'Ledger Entry', },
        { pathname: '/tax/create', title: 'Tax', },
        { pathname: '/discount/create', title: 'Discount', },
        { pathname: '/gift-card/create', title: 'Gift Card', },
    ]

    const AuthLinksList = authLinks.map((instance, index) => {

        if (!isAccessible(instance.path)) return null
        return <Dropdown.Item key={index}>
            <li>
                <span

                    className="dropdown-item"
                    onClick={() => _navigate(instance.pathname)}
                ><i className={`${instance.icon} mr-2`} /> {instance.title}</span>
            </li>
        </Dropdown.Item>
    })

    const CustomBell = React.forwardRef(({ children, onClick }, ref) => (
        <a
            href=""
            style={{ height: '70px', paddingTop: '26px', paddingBottom: '26px', cursor: 'pointer' }}
            ref={ref}
            onClick={e => {
                e.preventDefault();
                setTimeout(() => {
                    readNotifications({
                        "action": "read",
                        "notifications_id": notificationsArr.map(instance => instance.id)
                    })
                        .then(res => {
                            if (res.json.status) {
                                setUnreadCount(0)
                            }
                        })
                        .catch(err => console.log(err))
                }, 1800);
                onClick(e);
            }}
        >
            {/* Render custom icon here */}
            <i className="fa fa-bell-o" aria-hidden="true"></i>

            {unreadCount > 0 ? <Badge style={{ position: 'absolute', top: '20px', right: '-11px' }} pill variant="primary">
                {unreadCount}
            </Badge> : null
            }
        </a>
    ));


    useMousetrap('alt+i', () => props.history.push('/invoice/create'))
    useMousetrap('alt+p', () => props.history.push('/purchase-order/create'))

    const CustomAvatar = React.forwardRef(({ children, onClick }, ref) => (
        <a
            href=""
            style={{ height: '70px', paddingTop: '26px', paddingBottom: '26px', cursor: 'pointer' }}
            ref={ref}
            onClick={e => {
                e.preventDefault();
                onClick(e);
            }}
        >
            <i className="icon feather icon-user" />
        </a>
    ));

    const NotificationsLists_ = (arr) => {
        return arr.map((instance, index) => {
            return <li
                key={index}
                className="notification-list-item"
            // onClick={() => _navigate(`/${instance.model}/${instance.object_id}`)}
            >
                <a className="notification-item">
                    <i className="fa fa-bell-o m-r-20 f-20 p-10 p-l-0" aria-hidden="true"></i>
                    <div>
                        <p>
                            <strong>
                                {instance.model[0].toUpperCase() + instance.model.slice(1)}
                            </strong>
                            <span className="n-time text-muted">
                                <i className="icon feather icon-clock m-r-10" />
                                {(new Date(instance.date)).toDateString()}
                            </span>
                        </p>
                        <div className="notification-content">
                            {instance.msg}
                        </div>
                    </div>
                </a>
            </li>
        })
    }

    const EarlierList = NotificationsLists_(notificationsArr.filter(instance => instance.read))
    const NewList = NotificationsLists_(notificationsArr.filter(instance => !instance.read))

    return (
        <Aux>
            <ul className="navbar-nav ml-auto" >

                <li className="pr-0">
                    <NavLink title="User Guide" to='/user-manual'><i className="icon feather icon-help-circle f-16" /></NavLink>
                </li>
                <li>
                    <Dropdown alignRight={false} >
                        <Dropdown.Toggle variant={'link'} style={{ paddingRight: '5px' }} id="dropdown-basic">
                            Create
                        </Dropdown.Toggle>
                        <Dropdown.Menu alignRight >
                            <div className="createShortcutDropdown">

                                {createShortcuts.map((instance, index) => {
                                    if (isAccessible(instance.pathname)) {
                                        return <Dropdown.Item key={index}>
                                            <div onClick={() => _navigate(instance.pathname)} key={index} >{instance.title}</div>
                                        </Dropdown.Item>
                                    } else return null
                                })}

                            </div>
                        </Dropdown.Menu>
                    </Dropdown>
                </li>


                <li style={{ paddingRight: 0 }} className="notificationBell">
                    <Dropdown alignRight={true} >
                        <Dropdown.Toggle variant={'link'} id="dropdown-basic" as={CustomBell} >
                        </Dropdown.Toggle>
                        <Dropdown.Menu alignRight className="notification" >
                            <div className="noti-head d-flex justify-content-between">
                                <h6 style={{ lineHeight: '28px' }} className="d-inline-block m-b-0"><b>Notifications</b></h6>
                                <Dropdown.Item href="/notifications"><button className="btn btn-link">Show All</button></Dropdown.Item>
                            </div>
                            <ul className="noti-body">

                                {!isEmpty(NewList) ? <li className="n-title">
                                    <p className="m-b-0"><b>NEW</b></p>
                                </li> : null}
                                {NewList}
                                {!isEmpty(EarlierList) ? <li className="n-title">
                                    <p className="m-b-0"><b>EARLIER</b></p>
                                </li> : null}
                                {EarlierList}
                            </ul>
                        </Dropdown.Menu>
                    </Dropdown>
                </li>
                {/* <li className={props.rtlLayout ? 'm-r-15' : 'm-l-15'}>
                        <a className="displayChatbox" onClick={() => { setState({ listOpen: true }); }}><i className="icon feather icon-mail" /></a>
                    </li> */}



                <li>
                    <Dropdown alignRight={false} className="drp-user ">
                        <Dropdown.Toggle variant={'link'} id="dropdown-basic" as={CustomAvatar}>
                        </Dropdown.Toggle>
                        <Dropdown.Menu alignRight className="profile-notification">
                            <Dropdown.Item >
                                <div className="pro-head">
                                    <img src={Avatar} className="img-radius" alt="User Profile" />
                                    <span
                                        className="userFullName"
                                        onClick={() => _navigate('/profile')}
                                    >
                                        Profile
                                    </span>
                                </div>
                            </Dropdown.Item>
                            <ul className="pro-body">

                                {AuthLinksList}


                                <Dropdown.Item>
                                    <li>
                                        <span
                                            className="dropdown-item"
                                            onClick={_logout}
                                        ><b><i className="feather icon-log-out mr-2" />Log Out</b></span>
                                    </li>
                                </Dropdown.Item>
                            </ul>
                        </Dropdown.Menu>
                    </Dropdown>
                </li>
            </ul>
            <ChatList listOpen={state.listOpen} closed={() => { setState({ ...state, listOpen: false }); }} />
        </Aux >
    );
}

NavRight.propTypes = {
    logout: PropTypes.func.isRequired,
    auth: PropTypes.object.isRequired,
}

const mapStateToProps = state => ({
    auth: state.auth
})

export default connect(mapStateToProps, { logout })(withRouter(NavRight));
