import React from 'react';
import DEMO from './../../../../../globals/constant';
import Aux from "../../../../../hoc/_Aux";
import { NavLink } from 'react-router-dom';
import store from '../../../../../redux/store';
import mandalaLogo from '../../../../../assets/images/bizmandala.png'
import logoRight from '../../../../../assets/images/logoright.png'
import logoLeft from '../../../../../assets/images/logoleft.png'

const navLogo = (props) => {
    let toggleClass = ['mobile-menu'];
    if (props.collapseMenu) {
        toggleClass = [...toggleClass, 'on'];
    }

    window.title = DEMO.SYSTEM_NAME;
    return (
        <Aux>
            <div className="navbar-brand header-logo">
                <NavLink to='/dashboard' className="b-brand">

                    <div className="b-bg">
                        <img className="bizMandalaLogo" src={logoLeft} alt="" />
                    </div>
                    <span className="b-title"><img className="bizMandalaLogo logoRight" src={logoRight} alt="" /></span>

                </NavLink>
                <div className={toggleClass.join(' ')} id="mobile-collapse" onClick={props.onToggleNavigation}><span /></div>
            </div>
        </Aux>
    );
};

export default navLogo;
