import React, { useEffect } from 'react'
import { Route, Redirect, withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { isAccessible } from '../helpers/protectRoute';
import Jhilikka from '../App/components/Reusables/Jhilikka';
import { automaticLogout } from '../redux/actions/authActions';
import isEmpty from '../validation/isEmpty';

//-------if signed in enters to private route else to landing page
function ProtectedRoute({ component: Component, auth, settings, ...rest }) {

    const isPathAccessible = isAccessible(rest.path);
    const isAuthenticated = auth.isAuthenticated;

    useEffect(() => {
        if (isEmpty(settings) || isEmpty(auth)) {
            rest.automaticLogout();
        }
    }, [settings, auth, rest])


    let to = '/'

    // if authenticated but unauthorized
    if (isAuthenticated && !isPathAccessible) {
        Jhilikka.fire({ icon: 'error', title: "Not authorized, So you are redirected to dashboard!", background: '#ffe5de' })
        to = '/'
    }

    //if unauthenticated
    if (!isAuthenticated) {
        to = '/signin';
    }


    return (
        <Route
            {...rest}
            component={props => (isPathAccessible && isAuthenticated) ?
                (<Component {...props} />) :
                (<Redirect to={to} />)}
        />
    )
}


ProtectedRoute.propTypes = {
    auth: PropTypes.object.isRequired,
    settings: PropTypes.object.isRequired,
    automaticLogout: PropTypes.func.isRequired,
}


const mapStateToProps = state => ({
    auth: state.auth,
    settings: state.settings
})


export default withRouter(connect(mapStateToProps, { automaticLogout })(ProtectedRoute));
