import React, { Component } from 'react';
import { connect } from 'react-redux';
import ReactDOM from 'react-dom';

import NavLeft from "./NavLeft";
import NavRight from "./NavRight";
import Aux from "../../../../hoc/_Aux";
import DEMO from "../../../../globals/constant";
import * as actionTypes from "../../../../globals/actions";
import { withRouter, NavLink } from 'react-router-dom';
import store from '../../../../redux/store';
import mandalaLogo from '../../../../assets/images/bizmandala.png'

class NavBar extends Component {


    componentDidMount() {
        window.title = DEMO.SYSTEM_NAME
        ReactDOM.render(
            DEMO.SYSTEM_NAME,
            document.getElementsByTagName("title")[0]
        );

    }

    render() {
        let headerClass = ['navbar', 'pcoded-header', 'navbar-expand-lg', this.props.headerBackColor];
        if (this.props.headerFixedLayout) {
            headerClass = [...headerClass, 'headerpos-fixed'];
        }

        let toggleClass = ['mobile-menu'];
        if (this.props.collapseMenu) {
            toggleClass = [...toggleClass, 'on'];
        }




        return (
            <Aux>
                <header className={headerClass.join(' ')}>
                    <div className="m-header">
                        <div className={toggleClass.join(' ')} id="mobile-collapse1" href={DEMO.BLANK_LINK} onClick={this.props.onToggleNavigation}><span /></div>
                        <NavLink to='/dashboard' className="b-brand">
                            <div className="">
                                <img className="bizMandalaLogo" src={mandalaLogo} alt="" />
                            </div>
                        </NavLink>
                    </div>
                    <div className="mobile-menu" id="mobile-header" ><i className="feather icon-more-horizontal" /></div>
                    <div className="collapse navbar-collapse">
                        <NavLeft />
                        <NavRight rtlLayout={this.props.rtlLayout} />
                    </div>
                </header>
            </Aux>
        );
    }
}

const mapStateToProps = state => {
    return {
        rtlLayout: state.world.rtlLayout,
        headerBackColor: state.world.headerBackColor,
        headerFixedLayout: state.world.headerFixedLayout,
        collapseMenu: state.world.collapseMenu,
    }
};

const mapDispatchToProps = dispatch => {
    return {
        onToggleNavigation: () => dispatch({ type: actionTypes.COLLAPSE_MENU }),
    }
};



export default connect(mapStateToProps, mapDispatchToProps)(withRouter(NavBar));
