import React from 'react';

const SignUp = React.lazy(() => import('./Pages/Authentication/SignUp/SignUp'));
const Signin = React.lazy(() => import('./Pages/Authentication/SignIn/SignIn'));

const ForgotPassword = React.lazy(() => import('./Pages/Authentication/Password/ForgotPassword'));
const ResetPassword = React.lazy(() => import('./Pages/Authentication/Password/ResetPassword'));



const pubRoutes = [
    // { path: '/signup', exact: true, name: 'Signup', component: SignUp },
    { path: '/signin', exact: true, name: 'Signin', component: Signin },
    { path: '/forgot-password', exact: true, name: 'Forgot Password', component: ForgotPassword },
    { path: '/reset-password', exact: true, name: 'Reset Password', component: ResetPassword },
];

export default pubRoutes;