import { RESET_FETCH_MORE_OPTIONS, SET_FETCH_MORE_OPTIONS } from '../actions/types';


const initialState = {
    fetchMoreOptionsPlayground: false,
    fetchMoreOptionsItems: false,
    fetchMoreOptionsItemCategories: false,
    fetchMoreOptionsVendors: false,
    fetchMoreOptionsCustomers: false,
    fetchMoreOptionsAccounts: false, //for account types
    fetchMoreOptionsAccs: false, //for accounts
}

export default (state = initialState, action) => {
    switch (action.type) {
        case SET_FETCH_MORE_OPTIONS:
            return {
                ...state,
                [action.payload[0]]: action.payload[1]
            }
        case RESET_FETCH_MORE_OPTIONS:
            return {
                ...state,
                ...initialState
            }
        default:
            return state
    }
}