import { combineReducers } from 'redux';
import worldReducer from './worldReducer';
import authReducer from './authReducer';
import settingsReducer from './settingsReducer';
import selectDropdownReducer from './selectDropdownReducer';
import instanceCreationModalReducer from './instanceCreationModalReducer';

export default combineReducers({
    world: worldReducer,
    auth: authReducer,


    settings: settingsReducer,
    selectDropdown: selectDropdownReducer,
    instanceCreationModal: instanceCreationModalReducer

});