
import { LOGOUT, SET_USER } from "../../globals/actions";
import DEMO from '../../globals/constant'
import store from "../store";
import Jhilikka from "../../App/components/Reusables/Jhilikka";
import JwtDecode from "jwt-decode";
import { history } from "../..";
import { setSettings, resetSettings } from "./settingsActions";
import { baseRequest } from "../../api/base";


export const signin = (username, password) => async dispatch => {
    try {
        const res1 = await (await fetch(DEMO.SERVER_URL + '/api/v1/user/auth', {
            method: 'POST',
            body: JSON.stringify({
                username,
                password
            }),
            headers: {
                "Content-Type": "application/json"
            }
        })).json();
        console.log(res1);
        if (res1.error) {
            // console.log(res1.error)
            // console.log("some error has occured")
            return res1;
        }
        const res3 = await (await fetch(DEMO.SERVER_URL + '/api/v1/user/current', {
            method: 'GET',
            headers: {
                "Content-Type": "application/json",
                'Authorization': `JWT ${res1.token}`,
                // 'x-csrftoken': res2['x-csrftoken']
            }
        })).json();
        if (!res3.status) {
            Jhilikka.fire({ icon: 'error', title: res3.error, background: '#ffe5de' })
            return res3;
        }
        //save the data locally and dispatch the store
        //get user_id from the jwt
        const user = {
            user_id: JwtDecode(res1.token).user_id,
            first_name: res3.user.first_name,
            last_name: res3.user.last_name,
            // email: res3.user.email,
            role_str: res3.user.role_str,
            username: res3.user.username,
            accessToken: res1.token,
            powers: res3.user.powers,
            // csrfToken: res2['x-csrftoken']
        }



        const res4 = await (await fetch(DEMO.SERVER_URL + '/api/v1/user/settings/get', {
            method: 'GET',
            headers: {
                "Content-Type": "application/json",
                'Authorization': `JWT ${res1.token}`,
            }
        })).json();

        console.log(res4)

        if (!res4.status) {
            Jhilikka.fire({ icon: 'error', title: res4.json.error, background: '#ffe5de' })
            return res4;
        }

        console.log(user, 'going to be saved');
        localStorage.setItem('user_state', JSON.stringify(user))
        dispatch(setSettings(res4.settings))
        dispatch({ type: SET_USER, payload: user })


    } catch (err) {

        Jhilikka.fire({ icon: 'error', title: err, background: '#ffe5de' })
        console.log(err);
        return { err: err }
    }
}



//manual logout
export const logout = () => dispatch => {
    //do the call here
    console.log("here is the call")
    const jwt = store.getState().auth.user.accessToken;
    fetch(`${DEMO.SERVER_URL}${`/api/v1/user/logtime`}`, {
        method: 'GET',
        headers: {
            "Content-Type": "application/json",
            'Authorization': `JWT ${jwt}`,
        }
    })
        .then(res => res.json())
        .then(response => {
            console.log('here');
            store.dispatch(automaticLogout())
        });
}

//logout due to unauthorization
export const automaticLogout = () => dispatch => {
    //do the call here
    console.log("Trying to logout!")
    dispatch(resetUser());
    dispatch(resetSettings());
    history.push('/signin')
}



const resetUser = () => {
    localStorage.removeItem('user_state');
    localStorage.removeItem('user_settings')
    return {
        type: LOGOUT,
        payload: {},
    }
}