import { baseRequest } from '../base'




const getNotifications = async (data, signal) => {
    try {
        return await baseRequest("api/v1/user/notifications/get", "POST", data, signal);
    } catch (e) {
        alert(e);
    }
};

const readNotifications = async (data, signal) => {
    try {
        return await baseRequest("api/v1/user/notification/read", "POST", data, signal);
    } catch (e) {
        alert(e);
    }
};


export {
    getNotifications,
    readNotifications,
}